import { useLocation } from 'react-router';
import dailyui1 from '../assets/images/dailyui-1.jpg';
import dailyui2 from '../assets/images/dailyui-2.jpg';
import dailyui3 from '../assets/images/dailyui-3.jpg';
import dailyui4 from '../assets/images/dailyui-4.jpg';
import dailyui5 from '../assets/images/dailyui-5.jpg';
import dailyui6 from '../assets/images/dailyui-6.jpg';

const images = [{
    id: 1,
    title: "title",
    description: "Far far away, behind the world,",
    image: dailyui1,
  },
  {
    id: 2,
    title: "title2",
    description: "Far far away, behind the world,",
    image: dailyui2,
  },
  {
    id: 3,
    title: "title3",
    description: "Far far away, behind the world,",
    image: dailyui3,
  },
  {
    id: 4,
    title: "title4",
    description: "Far far away, behind the world,",
    image: dailyui4,
  },
  {
    id: 5,
    title: "title5",
    description: "Far far away, behind the world,",
    image: dailyui5,
  },
  {
    id: 6,
    title: "title6",
    description: "Far far away, behind the world,",
    image: dailyui6,
  },
];

const DailyUi = () => {
  const location = useLocation();
  const path = location.pathname.split("/")[2];
  const item = images.find((item) => item.id.toString() === path)
  console.log(location);
  return (
    <div className="dailyui">
      <div className="dailyui__wrapper">
        <div className="dailyui__content">
          <div className="dailyui__item">
            <figure className="dailyui__image"><img src={item.image} alt="" /></figure>
            <h2 className="dailyui__title">{item.title}</h2>
            <p className="dailyui__description">{item.description}</p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default DailyUi;