import React from "react";
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer className="footer">
      <p className="footer__text">A project made by <a href="https://www.facebook.com/profile.php?id=100022732097900" target="_blank" rel="noreferrer"
        className="link">Chibaosinger</a></p>
      <div className="footer__icons">

        <Link to="/about" rel="noreferrer"
          className="footer__icon linkedin"><span></span>
          <p>Linkedin</p>
        </Link>

        <Link to="/dribbble" target="_blank" rel="noreferrer"
          className="footer__icon dribbble"><span></span>
          <p>Dribbble</p>
        </Link>

        <Link to="/codepen" target="_blank" rel="noreferrer"
          className="footer__icon codepen"><span></span>
          <p>Codepen</p>
        </Link>
      </div>
    </footer>
  );
}

export default Footer;