import React, {useState, useEffect} from 'react';
import dailyui1 from '../assets/images/dailyui-1.jpg';
import dailyui2 from '../assets/images/dailyui-2.jpg';
import dailyui3 from '../assets/images/dailyui-3.jpg';
import dailyui4 from '../assets/images/dailyui-4.jpg';
import dailyui5 from '../assets/images/dailyui-5.jpg';
import dailyui6 from '../assets/images/dailyui-6.jpg';
import dailyui7 from '../assets/images/dailyui-7.jpg';
import dailyui8 from '../assets/images/dailyui-8.jpg';
import dailyui9 from '../assets/images/dailyui-9.jpg';
import dailyui10 from '../assets/images/dailyui-10.jpg';
import dailyui11 from '../assets/images/dailyui-11.jpg';
import dailyui12 from '../assets/images/dailyui-12.jpg';
import dailyui13 from '../assets/images/dailyui-13.jpg';
import dailyui14 from '../assets/images/dailyui-14.jpg';
import dailyui15 from '../assets/images/dailyui-15.jpg';

const Videos = () => {
  const [modal, setModal] = useState(false);
  const [videoSrc, setVideoSrc] = useState("");
  const videos = [{
        title: "Landing Page",
        image: dailyui1,
        video: "https://www.youtube.com/embed/fgOBjSFCDj8?rel=0&autoplay=1"
      },
      {
        title: "Ecommerce Shop",
        image: dailyui2,
        video: "https://www.youtube.com/embed/2HMnaD5887Q?rel=0&autoplay=1"
      },
      {
        title: "Slider Cards",
        image: dailyui3,
        video: "https://www.youtube.com/embed/hn7K-nTIm6I?rel=0&autoplay=1"
      },
      {
        title: "Landing Page",
        image: dailyui4,
        video: ""
      },
      {
        title: "Miku Login Form",
        image: dailyui5,
        video: "https://www.youtube.com/embed/tlBVFgzwhnQ?rel=0&autoplay=1"
      },
      {
        title: "Get Started",
        image: dailyui6,
        video: ""
      },
      {
        title: "Midu Subscribe",
        image: dailyui7,
        video: "https://www.youtube.com/embed/nMsIy8Y6tzE?rel=0&autoplay=1"
      },
      {
        title: "Dashboard",
        image: dailyui8,
        video: "https://www.youtube.com/embed/69lv4iVKYy8?rel=0&autoplay=1"
      },
      {
        title: "Subscribe",
        image: dailyui9,
        video: "https://www.youtube.com/embed/65YdekxRWPM?rel=0&autoplay=1"
      },
      {
        title: "Archi Landing Page",
        image: dailyui10,
        video: "https://www.youtube.com/embed/U6ZvO3wxTXM?rel=0&autoplay=1"
      },
      {
        title: "Agence Landing Page",
        image: dailyui11,
        video: "https://www.youtube.com/embed/KXPlO6njf9Q?rel=0&autoplay=1"
      },
      {
        title: "Sign In",
        image: dailyui12,
        video: "https://www.youtube.com/embed/dFFkuYJsoAY?rel=0&autoplay=1"
      },
      {
        title: "Film Fest Da Nang",
        image: dailyui13,
        video: "https://www.youtube.com/embed/LIH8lJZznBo?rel=0&autoplay=1"
      },
      {
        title: "CSS Buttons Hover Effect",
        image: dailyui14,
        video: "https://www.youtube.com/embed/I4XG2_6IfZQ?rel=0&autoplay=1"
      },
      {
        title: "Login & Signnup",
        image: dailyui15,
        video: "https://www.youtube.com/embed/aB5CWLumeyg?rel=0&autoplay=1"
      }
  ];

  useEffect(() => {
    ///////// Animate Modules //////////
    const videoItems = document.querySelectorAll('.videos__item'),
      mainTitle = document.querySelectorAll('.videos__mainTitle'),
      footer = document.querySelectorAll('footer'),
      footerLink = document.querySelectorAll('.link');
    let delay = 1;

    const anime = (element, animation) => {
      if (element.offsetParent != null) {

        if (!element.classList.contains(animation)) {
          element.classList.add(animation);

          element.style.animationDelay = `${delay}` * 0.2 + "s";
          delay++;
        }
      }
    };

    const isInViewport = (element) => {
      var bounding = element.getBoundingClientRect();
      return (
        bounding.bottom >= 0 &&
        bounding.right >= 0 &&
        bounding.top <= document.documentElement.clientHeight &&
        bounding.left <= document.documentElement.clientWidth
      )
    };

    const isItemVisible = (element, animation) => {
      if (isInViewport(element)) {
        if (window.innerWidth >= 300) {
          anime(element, animation);
        }
      }
    };

    // for viewport
    const animeItem = (item, animation) => {
      item.forEach(item => {
        isItemVisible(item, animation);
      })
      delay = 1;
    };

    // for scroll
    window.addEventListener('scroll', () => {
      if (window.innerWidth >= 300) {
        animeItem(videoItems, "anime");
        animeItem(mainTitle, "anime");
        animeItem(footerLink, "anime");
        animeItem(footer, "anime");
      }

    });
    // to load the animations
    animeItem(videoItems, "anime");
    animeItem(mainTitle, "anime");
    animeItem(footerLink, "anime");
    animeItem(footer, "anime");
  });

  return (
    <section className="videos">
      <div className="videos__wrapper">
        <div className="videos__heading">
          <h3 className="videos__mainTitle">works</h3>
        </div>
        <div className="videos__content">
          {videos.map((item, index) => (
          <div key={index} className="videos__item" onClick={() => {setModal(true);setVideoSrc(item.video); document.body.classList.add('noscroll');}}>
            <p className="videos__title">{item.title}</p>
            <span className="videos__icon"></span>
            <img src={item.image} alt="" />
          </div>
          ))}
        </div>
      </div>
      <div className={modal ? "modal open" : "modal"}>

        <div className="modal__container">
          <i className="modal__icon" onClick={() => {setModal(false); setVideoSrc(""); document.body.classList.remove('noscroll');}}><svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="none" viewBox="0 0 32 32" > <path fill="#202842" d="M17.88 16l5.733-5.72a1.339 1.339 0 10-1.893-1.893L16 14.12l-5.72-5.733a1.339 1.339 0 10-1.893 1.893L14.12 16l-5.733 5.72a1.333 1.333 0 00-.008 1.886l.008.007a1.333 1.333 0 001.885.008l.008-.008L16 17.88l5.72 5.733a1.333 1.333 0 001.893 0 1.333 1.333 0 00.008-1.885l-.008-.008L17.88 16z" ></path> </svg></i>

          <div className="modal__video">
            <div className="modal__code">
              <iframe title="dailyui" width="1920" height="1080" src={videoSrc} frameBorder="0"
                  allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                  allowFullScreen></iframe>
            </div>
          </div>

        </div>

      </div>
    </section>
  );
}

export default Videos;