import React, { useState, useEffect } from "react";

const BackTop = () => {
  const [showBackTop, setShowBackTop] = useState(false);
  
  useEffect(() => {
    window.addEventListener('scroll', function () {
      if (window.pageYOffset >= 400) {
        setShowBackTop(true);
      } else {
        setShowBackTop(false);
      }
    });

    // Back to top
    var backTop = document.querySelector(".backTop");

    backTop.onclick = () => {
      scrollToTop(400);
    }

    function scrollToTop(scrollDuration) {
      var scrollStep = -window.scrollY / (scrollDuration / 15),
        scrollInterval = setInterval(function () {
          if (window.scrollY !== 0) {
            window.scrollBy(0, scrollStep);
          } else clearInterval(scrollInterval);
        }, 15);
    }

    const observer = new IntersectionObserver(entries => {
      entries.forEach(entry => {
        console.log(entry)
        if (entry.intersectionRatio > 0) {
          backTop.style.bottom = entry.intersectionRect.bottom - entry.boundingClientRect.top + 24 + 'px';

        if (window.innerWidth <= 799) {
          backTop.style.bottom = entry.intersectionRect.bottom - entry.boundingClientRect.top + 70 + 'px';
        }
          observer.unobserve(entry.target);
        } else {
          backTop.style.bottom = '24px';

          if (window.innerWidth <= 799) {
            backTop.style.bottom = '70px';
          }

        }

      });
    });

    window.addEventListener('scroll', function () {
      observer.observe(document.querySelector('footer'));
    })
  });

  /* end Back to top */

  return (
    <div className={showBackTop ? "backTop show" : "backTop"}></div>
  );
}

export default BackTop;