import React, {useState, useEffect} from "react";
import { Link } from 'react-router-dom';
import iconSearch from '../assets/icons-base/icon-search.svg';
import imgProfile from '../assets/profile.jpg';

function MainNav() {
  const [sticky, setSticky] = useState(false);
  const [navOpen, setNavOpen] = useState('');
  const navLinks = [{
      name: "About Us",
      path: "/about"
    },
    {
      name: "Gallery",
      path: "/gallery"
    },
    {
      name: "Videos",
      path: "/videos"
    },
    {
      name: "Contacts",
      path: "/contacts"
    },
  ];
  
  useEffect(() => {
    window.onscroll = () => {
      if (window.pageYOffset >= 120) {
        setSticky(true);
      } else {
        setSticky(false);
      }
    };

    window.onresize = () => {
      let mainNavMobile = document.querySelector('.mainNav__mobile');
      if (!mainNavMobile.classList.contains('open-menu')) {
        setNavOpen('');
      }
      
      if (window.innerWidth > 767) {
        mainNavMobile.style.display = "none";
        document.body.classList.remove("noscroll");
      } else {
        mainNavMobile.style.display = "block";
        if (mainNavMobile.classList.contains('open-menu')) {
          setNavOpen('open-menu');
          document.body.classList.add("noscroll");
        }
      }
    }
  });

  return (
    <nav className={sticky ? "mainNav sticky" : "mainNav"}>
      <div className={`mainNav__mobile ${navOpen}`}>
        <div className="mainNav__mobileLinks">
          {navLinks.map((link, index) => (
            <Link to={link.path} key={index} className={navOpen === "open-menu" ? "mainNav__mobileLink anime-menu-links" : "mainNav__mobileLink"} onClick={() => {setNavOpen('close-menu'); document.body.classList.remove("noscroll");}}>{link.name}</Link>
          ))}
        </div>
        <div className="mainNav__menuIcon close" onClick={() => {setNavOpen('close-menu'); document.body.classList.remove("noscroll");}}>
          <span></span>
        </div>
      </div>

      <div className="mainNav__content">
        <Link to="/" className="mainNav__logo">Logo</Link>

        <div className="mainNav__links">
          {navLinks.map((link, index) => (
            <Link to={link.path} key={index} className="mainNav__linkItem">{link.name}</Link>
          ))}
        </div>

        <div className="mainNav__actions">
          <div className="mainNav__search">
            <input type="search" placeholder="Search..." />
            <button className="mainNav__searchBtn"><img src={iconSearch} alt="" /></button>
          </div>

          <div className="mainNav__upload"><svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="none" viewBox="0 0 32 32" > <g fill="#000" clipPath="url(#clip0)"> <path d="M9.143 10.285a.57.57 0 00.404-.167l5.882-5.882v20.335a.571.571 0 101.142 0V4.236l5.882 5.882a.57.57 0 00.808 0 .57.57 0 000-.808l-6.856-6.857a.572.572 0 00-.81 0L8.74 9.31a.57.57 0 00.404.975z"></path> <path d="M31.429 21.714a.571.571 0 00-.572.571v6.286H1.143v-6.286a.571.571 0 10-1.143 0v7.429h32v-7.429a.572.572 0 00-.571-.571z"></path> </g> <defs> <clipPath id="clip0"> <path fill="#fff" d="M0 0H32V32H0z"></path> </clipPath> </defs> </svg></div>

          <div className="mainNav__profile">
            <div className="mainNav__profileImage"><img src={imgProfile} alt="" /></div>
            <div className="mainNav__profileMenu">
              <a href="https://dailyuiapp.firebaseapp.com/web/#about" className="mainNav__profileLink account"><svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="none" viewBox="0 0 32 32" > <g clipPath="url(#clip0)"> <path fill="#202842" d="M28.509 31.186v-4.487c0-5.553-3.64-10.272-8.65-11.9a7.916 7.916 0 004.043-6.897C23.902 3.547 20.355 0 16 0S8.098 3.547 8.098 7.902A7.916 7.916 0 0012.14 14.8c-5.016 1.628-8.65 6.347-8.65 11.9v4.487c0 .45.364.814.814.814h23.403a.815.815 0 00.8-.814zM9.713 7.902a6.29 6.29 0 016.28-6.28 6.29 6.29 0 016.281 6.28 6.29 6.29 0 01-6.28 6.281 6.29 6.29 0 01-6.281-6.28zm17.174 22.47H5.1v-3.68c0-6.003 4.885-10.894 10.894-10.894 6.01 0 10.894 4.885 10.894 10.894v3.68z" ></path> </g> <defs> <clipPath id="clip0"> <path fill="#fff" d="M0 0H32V32H0z"></path> </clipPath> </defs> </svg>
                Profile</a>
              <a href="https://dailyuiapp.firebaseapp.com/web/#about" className="mainNav__profileLink"><svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="none" viewBox="0 0 32 32" > <g fill="#202842" clipPath="url(#clip0)"> <path d="M5.588 27.525c.207.148.476.179.712.083l8.166-3.343a.74.74 0 00.306-.233L26.716 8.61a4.67 4.67 0 00-.878-6.586L24.464.986a4.686 4.686 0 00-6.51.871L5.95 17.39a.745.745 0 00-.154.411l-.519 9.079a.742.742 0 00.31.646zm1.607-8.21l5.456 4.09-5.826 2.384.37-6.475zm6.826 3.265l-6.463-4.847L17.306 5.12l6.447 4.894L14.02 22.58zm5.106-19.816a3.199 3.199 0 014.443-.595l1.374 1.038a3.187 3.187 0 01.6 4.496l-.883 1.14-6.449-4.896.915-1.183zM31.259 30.52H.74a.741.741 0 100 1.483H31.26a.741.741 0 000-1.483z"></path> </g> <defs> <clipPath id="clip0"> <path fill="#fff" d="M0 0H32V32H0z"></path> </clipPath> </defs> </svg>
                Edit profile</a>
              <a href="https://dailyuiapp.firebaseapp.com/web/#about" className="mainNav__profileLink"><svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="none" viewBox="0 0 32 32" > <path fill="#202842" d="M22.083 30.435H9.917a3.583 3.583 0 01-3.13-1.703L.395 17.672a2.875 2.875 0 010-2.908l6.392-11.06a3.583 3.583 0 013.13-1.703h12.166a3.583 3.583 0 013.13 1.78l6.392 11.06a2.875 2.875 0 010 2.909l-6.392 11.06a3.585 3.585 0 01-3.13 1.625zM9.917 3.737a1.958 1.958 0 00-1.703.874L1.822 15.67a1.217 1.217 0 000 1.25L8.214 27.98a1.958 1.958 0 001.703.874h12.166a1.958 1.958 0 001.703-.874l6.392-11.06a1.216 1.216 0 000-1.25L23.786 4.61a1.958 1.958 0 00-1.703-.873H9.917z" ></path> <path fill="#202842" d="M16 22.616a6.36 6.36 0 116.36-6.36 6.37 6.37 0 01-6.36 6.36zm0-11.06a4.7 4.7 0 100 9.402 4.7 4.7 0 000-9.402z" ></path> </svg>
                Setting account</a>
              <a href="https://dailyuiapp.firebaseapp.com/web/#about" className="mainNav__profileLink"><svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="none" viewBox="0 0 32 32" > <g fill="#202842" clipPath="url(#clip0)"> <path d="M20.177 2.881a.57.57 0 10-.32 1.097c5.804 1.697 9.857 7.11 9.857 13.165 0 7.561-6.152 13.714-13.714 13.714S2.286 24.705 2.286 17.143c0-6.062 4.06-11.477 9.873-13.17a.571.571 0 00-.318-1.097c-6.3 1.833-10.698 7.7-10.698 14.267C1.143 25.335 7.807 32 16 32s14.857-6.665 14.857-14.857c0-6.56-4.392-12.423-10.68-14.262z"></path> <path d="M16 15.429a.57.57 0 00.571-.572V.571a.57.57 0 10-1.142 0v14.286a.57.57 0 00.571.572z"></path> </g> <defs> <clipPath id="clip0"> <path fill="#fff" d="M0 0H32V32H0z"></path> </clipPath> </defs> </svg>
                Log out</a>
            </div>
          </div>

          <div className="mainNav__menuIcon open" onClick={() => {setNavOpen('open-menu'); document.body.classList.add("noscroll");}}>
            <span></span>
          </div>
          
        </div>
      </div>
    </nav>
  );
}
  
export default MainNav;
  